body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-xl: 20px;
  --font-size-3xl: 22px;
  --font-size-29xl: 48px;
  --font-size-sm: 14px;

  /* Colors */
  --color-darkslategray: #2e2e2e;
  --color-white: #fff;
  --color-gray-100: #1b1d1c;
  --color-gray-200: rgba(23, 23, 23, 0.8);
  --color-gray-300: rgba(255, 255, 255, 0.14);
  --color-red: #ea1821;
  --color-whitesmoke-100: #eee;
  --color-whitesmoke-200: #ececec;
  --color-black: #000;
  --color-slateblue: rgba(82, 52, 163, 0.78);

  /* Border radiuses */
  --br-9xs: 4px;
  --br-xl: 20px;
  --br-3xs: 10px;
  --br-8xs-5: 4.5px;
  --br-6xl: 25px;
  --br-11xs: 2px;
  --br-mini: 15px;
}
